<template>
	<div :class="{NoteContainer:true, selected:isSelected}" @click.stop="onClick" style="padding: 2px">
		
		<!-- <div class=flexbox style="{width: 100%, height: 100%, background-color:grey;}"> 
			
			<button v-on:click="buttonClick" v-bind:class="{focused: buttonIsFocused}"> {{button_text}} </button>
			
			<p v-on:click="pClick" v-bind:class="{focused: pIsFocused}"> root </p>
		
		</div> -->

		<NoteInstance class="instance" v-for="child in children" :note_id="child" :parent_note_id="note_id"/>
		
	</div>
</template>

<script>
import {mapState, mapGetters} from 'vuex';
import NoteInstance from './NoteInstance.vue'

const uuidv1 = require('uuid/v1');
export default {

	name: 'NoteContainer',
	
	components: {
		NoteInstance
	},

	data(){
		
		return {
			id: 'root',
			note_id: 'root',
			focusedInstance: null, // instanced is focused iff it is currently a textarea.
			selectedInstances : [],
			root: this,
			is_root: true,
			clipboard : {
				notes : []

			}
		}
	},

	methods: {

		registerNewChild(){
			var new_note_id = uuidv1();
			this.$store.commit('registerNewNote', new_note_id);
			this.$store.commit('addChildToParent', [new_note_id, this.note_id]);
		},


		removeNoteFromParent(){
			var child = this.note; 
			var parent = this.parentNote;
			this.$store.commit('removeChildFromParent',{child, parent});
		},


		focus(instance){
			this.focusedInstance = instance;
		},

		selectThis(){
			this.selectInstance(this);
		},

		selectInstance(instance,append=false){
			if(this.selectedInstances.includes(instance)){return;}

			if (append){
				this.selectedInstances.push(instance)	
			}
			else{
				this.selectedInstances = [instance];
				this.focusedInstance = null;
			}
		},

		deselectInstance(instance){
			this.selectedInstances = this.selectedInstances.filter( (i) => (i!=instance) );
		},

		instanceIsSelected(instance){ 
			 
			return this.selectedInstances.includes(instance)
		},

		onClick(e){
			this.$emit('click',{instance: this, event: e});
		},

		getNewNote(){
			var newNoteId = uuidv1();
			this.$store.commit('registerNewNote',newNoteId);
			var newNote = this.$store.getters.getNoteById(newNoteId); 
			return newNote;
		},
		

		//next instance below this on screen. Wraps around.
		nextVisibleInstance(instance){
			if (instance.is_root){return this.$children.length>0 ? this.$children[0] : this}

			if (instance.isExpanded && instance.$children.length>0){return instance.$children[0]}

			return this.nextSiblingDown(instance);
		},

		//goes up to closest ancestor that has a next sibling and returns that. Next sibling of last top-level instance is root.	
		nextSiblingDown(instance){
			if (instance.is_root){return instance.$children.length>0 ? instance.$children[0] : instance}
			if (!instance.isLastSibling()){return instance.nextSibling()}
			return (instance.$parent.is_root ? instance.root : this.nextSiblingDown(instance.$parent))		
		},

		//Assumes instance is visible
		previousVisibleInstance(instance){
			if (instance.is_root){return this.$children.length>0 ? this.$children[this.$children.length-1] : this}

			var i = instance.previousSibling(); 
			var next;
			while (true){
				next = this.nextVisibleInstance(i);
				if (next == instance){ return i }
				else { i = next }
			}

		},

		//Binds <fn> to root then calls <fn> for <all/some> selected instances, <including/excluding> root.
		callFn(fn,forAllSelectedInstances=true,isDoableOnRoot=false){
			fn = fn.bind(this);
			var instances = forAllSelectedInstances ? this.selectedInstances.filter(()=>(true)) : [this.selectedInstances[0]];
			var cycle = function(){
				var instance = instances.pop();
				if (isDoableOnRoot || !instance.is_root){fn(instance)}
				if (instances.length>0){this.$nextTick(cycle)}
			}.bind(this);
			cycle();
		},

		deleteInstance(instance){
			// If multiple instances are deleted at once, this instances parent might have been deleted first.
			// In this case, we can't unselect it, since it's not on the screen, but we still need to unlink it's note.
			if(!instance.wasDeleted()){
				this.selectInstance(this.previousVisibleInstance(instance),true);
				this.deselectInstance(instance);
			}
			
			this.$store.commit('removeChildFromParent',{child: instance.note, parent: instance.$parent.note});

		},

		addToClipboard(instance){
			this.clipboard.notes.push(instance.note);
		},

		attachAllNotesOnClipboard(instance){
			this.clipboard.notes.forEach(function(note){
				this.$store.commit('addChildToParent',{child: note, parent:instance.note});
			}.bind(this));
		},

		

		//Many functions that act on instances also act on the container. These fns are simple counterparts of noteinstance fns. 
		path(){return [this]},
		expand(){}, 
		isVisible(){return true},
		wasDeleted(){return false}


	},
 
	computed: {
		...mapState({
			children(state){
				var children = state.notes[this.note_id].children;
				return children;
			},

			note(state){
				return state.notes[this.note_id];
			}
		}),

		isSelected(){ return this.instanceIsSelected(this)},

		firstSelectedInstance(){ return this.selectedInstances[0] } 

	},


	created(){
		document.addEventListener('keydown', e => {
			if (this.selectedInstances.length == 0){return;}
			if (this.focusedInstance != null && e.key!='Alt'){return;}
			switch(e.key){
				case 'Alt':
					e.preventDefault();
					this.focusedInstance = null;
					break;

				case 'Insert':
					//Design deicision: clicking insert with multiple instances selected creates ONE new note and adds it to all selected.
					
					var newNote = this.getNewNote();
					var copy = this.selectedInstances.filter(()=>(true));
					this.selectedInstances = [];
					var root = this;

					copy.forEach(function(instance){
						root.$store.commit('addChildToParent',{child: newNote, parent: instance.note});
						instance.expand();
						vue.$nextTick(()=>{
							instance.$children.forEach((childInstance)=>{
								if(childInstance.note==newNote){root.selectInstance(childInstance)}
							})
						})
						root.selectInstance(instance,true);
					})
					break;

				case 'Delete': 
					this.callFn(this.deleteInstance);
					break; 

				case 'c':
					if (e.ctrlKey){
						this.clipboard.notes = [];
						this.callFn(this.addToClipboard);
					}
					break;

				case 'v': 
					if (e.ctrlKey && this.focusedInstance==null){
						this.callFn(this.attachAllNotesOnClipboard,true,true)
					}
					break;

				case 'Enter':
					if (this.selectedInstances.length == 1){
						var inst = this.selectedInstances[0];
						if (inst != this){
							var ta = inst.textArea;
							this.focus(inst);
							this.$nextTick(function(){
								ta.focus();
							});
						}
					}
					break;

				case 'ArrowUp':
					if (this.focusedInstance != null){return;}
					e.preventDefault();
					this.selectInstance(this.previousVisibleInstance(this.selectedInstances[0]));
					break;

				case 'ArrowDown':
					if (this.focusedInstance != null){return;}
					e.preventDefault();
					if (e.Shift) {this.selectInstance(this.selectedInstances[0].nextSibling)}
					this.selectInstance(this.nextVisibleInstance(this.selectedInstances[0]));
					break;

				case 'Tab':
					e.preventDefault();
					var inst = this.selectedInstances[0];
					if(inst==this){return;}
					inst.toggleChildren();
					break;	



			}
		});	
	},

	mounted(){
		this.$on('click',function({instance, event}){
			if (event.ctrlKey){
				this.selectInstance(instance,true);
			}

			else { 
				if(this.instanceIsSelected(instance)){
					this.focusedInstance = instance
				}

				else {
					this.selectInstance(instance);	
					this.focusedInstance = null;
				}
				
			}

		})
	}
}

</script>

<style scoped>
	
	.NoteContainer {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 100%; 
		background-color: grey;
		margin: 3px;
		overflow: scroll;
	}

	.instance {
		display: block;
		width: 100%;
		padding: 1px;
	}

	.selected{ 
	    background-color: #8C8888;
	    box-shadow: 0 0 10px red;
	}


</style>